ttom: 30px;\n        margin-top: 0px;\n    }\n\n    .sidebar-video-thumb{\n        float: left;\n        width: 100%;\n    }\n\n    .sidebar-video-container{\n        float: left;\n        width: 100%;\n        margin-bottom: 20px;\n        padding: 0px 30px;\n    }\n\n    .sidebar-video-block{\n        text-align: center;\n    }\n\n    .sidebar-video-block iframe {\n        border: none;\n        z-index: -1;\n        position: relative;\n    }\n\n    .sidebar-video-content{\n        float: left;\n        width: 100%;        \n        padding: 0px 10px 0px;\n        line-height: 16px;\n    }\n\n    .sidebar-video-content .video-title{\n        font-size: 14px;\n        color: #62696d;\n        font-family: montserratregular;\n        display: block;\n        padding-top: 5px;\n    }\n\n    .sidebar-video-content .now-playing{\n        color: #fb545b;\n        font-size: 10px;\n        font-family: montserratregular;\n    }\n\n    .sidebar-video-content .now-playing i.material-icons {\n        font-size: 10px;\n        position: relative;\n        top: 1px;\n    }\n\n    .modal-video-full {\n        float: left;\n        width: 100%;\n        /*width: calc(100% - 272px);*/\n        /*height: 100vh;*/\n        /*position: relative;\n        top: 0;\n        bottom: 0;\n        left: 0;*/\n        /*background: rgba(255, 255, 255, 0.95);*/\n        /*overflow-y: scroll;*/\n        /*padding: 30px 65px 55px;*/\n        text-align: center;\n    }\n\n    .modal-video-full h3{\n        font-size: 16px;\n        color: #62696d;\n        font-family: montserratregular;\n        margin-top: 0;    \n        margin-bottom: 32px;\n    }\n\n    .modal-video-full-block{\n        float: left;\n        width: 100%;\n        /*max-height: 528px;*/\n    }\n\n    .modal-video-full-block iframe{\n        /*border: none;\n        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.28);*/\n    }\n\n    #video-modal button.close.btn-close {\n        position: absolute;\n        top: -30px;\n    